@import url(https://fonts.googleapis.com/css2?family=Oxygen&display=swap);
:root {
  --primary-dark: #5EBEA3;
  --primary-light: #5EBEA3;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Oxygen', sans-serif;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 2rem;
}

.container {
  max-width: 1240px;
  margin: auto;
  padding: 0 1rem;
}

button {
  background-color: #5EBEA3;
  box-shadow: #094c66 4px 4px 0px;
  border-radius: 8px;
  transition: box-shadow 200ms, -webkit-transform 200ms;
  transition: transform 200ms, box-shadow 200ms;
  transition: transform 200ms, box-shadow 200ms, -webkit-transform 200ms;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: .7rem 1.5rem;
  cursor: pointer;
}

button:active {
  -webkit-transform: translateY(4px) translateX(4px);
          transform: translateY(4px) translateX(4px);
  box-shadow: #094c66 0px 0px 0px;
}

@media screen and (max-width: 940px) {
  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 1.5rem;
  }
}


.destinations {
    width: 100%;
    text-align: center;
    padding: 4rem 0;
}

.destinations h1 {
    padding: 1rem;
}

.img-container {
    --gap:1rem;
    --num-cols:5;
    --row-height:300px;

    display: grid;
    grid-template-columns: repeat(var(--num-cols), 1fr);
    margin: auto;
    padding:  1rem 0;
    grid-gap: var(--gap);
    gap: var(--gap);
}

.destinations img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.span-3 {
    grid-column: span 3;
}

.image-grid-row-2 {
    grid-row: span 2;
}

@media screen and (max-width: 940px) {
    .img-container {
        --gap: .5rem;
        --num-cols: 2;
        --row-height: 200px;
    }

    .span-3 {
        grid-column: span 2;
    }
}


.hero {
    width: 100%;
    height: 100vh;
    color: white;
    position: relative;
    background-image: linear-gradient(to left top, #223551, #00658c, #00989d, #00c776, #a8eb12);
    /* color: black; */
}
.hero .content {
    position: absolute;
    top: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: 100%;
    margin: auto;
    padding: 1rem;
}

.form {
    display: flex;
    background-color: #ffffffcc;
    color: black;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #ccc;
    padding: 4px;
    border-radius: 8px;
    max-width: 700px;
    width: 100%;
    margin: 0 auto;
}

.form input[type=text] {
    border: transparent;
    background-color: transparent;
    width: 400px;
    font-size: 1.2rem;
    font-family: 'Oxygen', sans-serif;
}

.form input[type=text]:focus {
    outline: none;
}

@media screen and (max-width: 940px) {
    .form input[type=text] {
       max-width: 300px;
    }
}
.navbar {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    z-index: 2;
    position: absolute;
    /* color: white; */
}

.navbar-bg {
    background-color: whitesmoke;
}

.nav-menu {
    display: flex;
}

li {
    padding: 1rem;
}

.nav-icons .icon:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    transition: 0.3s;
}

.hamburger {
    display: none;
    padding: 1rem;
}

.mobile-menu {
    display: none;
}

@media screen and (max-width: 940px) {

    .mobile-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        top: -100vh;
        left: 0;
        position: absolute;
        justify-content: center;
        /* align-items: center; */
        /* color: rgba(255, 255, 255, .95); */
        background-color: whitesmoke;
        transition: 0.3s;
        color: black;
    }
    
    .dark {
        color: black;
    }

    .active {
        top: 80px;
    }

    .mobile-menu li {
        padding: 1.2rem 1rem;
        margin:  0 1rem;
        border-bottom: 1px solid #ccc;
    }

    .mobile-nav {
        margin: 1rem 0;
    }

    .mobile-menu-bottom {
        width: 100%;
        text-align: center;
        padding: 1rem 0;
    }

    .mobile-menu button {
        margin: 1rem;
        width: 90%;
    }
    .nav-menu {
        display: none;
    }

    .nav-icons {
        display: none;
    }

    .hamburger {
        display: block;
    }
}
.tcomponent {
    width: 100%;
    text-align: center;
    padding: 4rem 0; 
}

.tcomponent h3 {
    padding: 1rem;
}

.container h2 {
    padding: 1rem;
}


.img-container {
    display: grid;
    margin: auto;
    padding:  1rem 1rem;
    margin-top: 3rem;
}

/* .flex {
    display: flex;
    flex-direction: row;
} */

.tcomponent img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Responsive cards without media query */
.cards {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    padding:  1rem 1rem;
  }
  
  .cards:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

  .card{ 
    padding: 2px 16px;
  }
  

